function Footer() {
  return (
    <>
      <div className="container-fluid bg-dark text-secondary mt-5 pt-5">
        <div className="row px-xl-5 pt-5">
          <div className="col-lg-4 col-md-12 mb-5 pr-3 pr-xl-5">
            <h5 className="text-secondary text-uppercase mb-4">Get In Touch</h5>
            <p className="mb-4">
              Wed love to hear from you! Whether you have questions, feedback,
              or just want to say hello, feel free to reach out. Our team is
              always ready to assist you.
            </p>
            <p className="mb-2">
              <i className="fa fa-map-marker-alt text-primary mr-3" />
              123 Street, Cairo, Egypt
            </p>
            <p className="mb-2">
              <i className="fa fa-envelope text-primary mr-3" />
              info@haneedy.com
            </p>
            <p className="mb-0">
              <i className="fa fa-phone-alt text-primary mr-3" />
              +012 345 67890
            </p>
          </div>
          <div className="col-lg-8 col-md-12">
            <div className="row">
              <div className="col-md-4 mb-5">
                <h5 className="text-secondary text-uppercase mb-4">
                  Quick Shop
                </h5>
                <div className="d-flex flex-column justify-content-start">
                  <a className="text-secondary mb-2" href="/">
                    <i className="fa fa-angle-right mr-2" />
                    Home
                  </a>
                  <a className="text-secondary mb-2" href="/shop">
                    <i className="fa fa-angle-right mr-2" />
                    Our Shop
                  </a>
                  <a className="text-secondary mb-2" href="/cart">
                    <i className="fa fa-angle-right mr-2" />
                    Shopping Cart
                  </a>
                  <a className="text-secondary mb-2" href="/checkout">
                    <i className="fa fa-angle-right mr-2" />
                    Checkout
                  </a>
                  <a className="text-secondary" href="/contact">
                    <i className="fa fa-angle-right mr-2" />
                    Contact Us
                  </a>
                </div>
              </div>
              <div className="col-md-4 mb-5"></div>
              <div className="col-md-4 mb-5">
                <h5 className="text-secondary text-uppercase mb-4">
                  New seller ?
                </h5>
                <p>Contact US!</p>
                <form
                  onSubmit={(e) => {
                    e.preventDefault();
                    const userEmail = e.target.elements.userEmail.value;

                    const gmailUrl = `https://mail.google.com/mail/?view=cm&fs=1&to=info@haneedy.com&su=${encodeURIComponent(
                      "I'm New Seller!"
                    )}&body=${encodeURIComponent("Type Your Message Here.")}`;

                    window.open(gmailUrl, "_blank");
                  }}
                >
                  <div className="input-group">
                    <input
                      type="email"
                      name="userEmail"
                      className="form-control"
                      placeholder="Your Email Address"
                      required
                    />
                    <div className="input-group-append">
                      <button type="submit" className="btn btn-primary">
                        Contact Now
                      </button>
                    </div>
                  </div>
                </form>

                <h6 className="text-secondary text-uppercase mt-4 mb-3">
                  Follow Us
                </h6>
                <div className="d-flex">
                  <a className="btn btn-primary btn-square mr-2" href="#">
                    <i className="fab fa-twitter" />
                  </a>
                  <a className="btn btn-primary btn-square mr-2" href="#">
                    <i className="fab fa-facebook-f" />
                  </a>
                  <a className="btn btn-primary btn-square mr-2" href="#">
                    <i className="fab fa-linkedin-in" />
                  </a>
                  <a className="btn btn-primary btn-square" href="#">
                    <i className="fab fa-instagram" />
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          className="row border-top mx-xl-5 py-4"
          style={{ borderColor: "rgba(256, 256, 256, .1) !important" }}
        >
          <div className="col-md-6 px-xl-0">
            <p className="mb-md-0 text-center text-md-left text-secondary">
               <span className="text-warning">All Rights Reserved.</span>{" "}
              Designed by{" Haneedy Team "}
              <a className="text-primary" href="https://depi.gov.eg/">
                DEPI
              </a>
            </p>
          </div>
          <div className="col-md-6 px-xl-0 text-center text-md-right">
            <img className="img-fluid" src="img/payments.png" alt="" />
          </div>
        </div>
      </div>
    </>
  );
}

export default Footer;
