import ReactDOM from "react-dom/client";
import App from "./App";
import { AllData } from "./Data/AllData";
import { createBrowserRouter, RouterProvider } from "react-router";
import AllShop from "./Shop/AllShop";
import AllContact from "./Contact/AllContact";
import AllCheckout from "./Checkout/AllCheckout";
import AllShopCart from "./ShopCart/AllShopCart";
import AllSingleProduct from "./SingleProduct/AllSingleProduct";
import { CartProvider } from "react-use-cart";
import "bootstrap-icons/font/bootstrap-icons.css";
import { FavoritesProvider } from "./context/FavoritesContext";
import Favorites from "./ShopFavorites/Favorites";


let router = createBrowserRouter([
  {
    path: "/",
    element: <App />,
  },
  {
    path: "shop",
    element: <AllShop />,
  },
  {
    path: "contact",
    element: <AllContact />,
  },
  {
    path: "checkout",
    element: <AllCheckout />,
  },
  {
    path: "shopcart",
    element: <AllShopCart />,
  },
  {
    path: "singleproduct/:id",
    element: <AllSingleProduct />,
  },
  {
    path: "shopfavorites",
    element: <Favorites />,
  },
]);

ReactDOM.createRoot(document.getElementById("root")).render(
  <AllData>
    <FavoritesProvider>
      <CartProvider>
        <RouterProvider router={router} />
      </CartProvider>
    </FavoritesProvider>
  </AllData>
);
