import React, { useContext, useState } from "react";
import { useParams } from "react-router-dom";
import { apiValue } from "../Data/AllData";
import AllCompo from "../Components/AllCompo";
import Footer from "../Components/Footer";
import { useCart } from "react-use-cart";
import { useFavorites } from "../context/FavoritesContext";

function AllSingleProduct() {
  const { id } = useParams();
  const data = useContext(apiValue);

  const product = data.find((item) => item.id == id);

  const [mainImg, setMainImg] = useState(product?.images[0]);
  const [zoom, setZoom] = useState(false);

  const { addItem, updateItemQuantity, items } = useCart();
  const { favorites, addToFavorites, removeFromFavorites } = useFavorites();

  if (!product) {
    return <h2 className="text-center mt-5">Product Not Found</h2>;
  }

  const isFavorite = favorites.find((i) => i.id === product.id);
  const inCart = items.find((i) => i.id === product.id);

  return (
    <>
      <AllCompo />

      {/* Breadcrumb Start */}
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <a className="breadcrumb-item text-dark" href="/">
                Home
              </a>
              <a className="breadcrumb-item text-dark" href="/shop">
                Shop
              </a>
              <span className="breadcrumb-item active">Product Details</span>
            </nav>
          </div>
        </div>
      </div>
      {/* Breadcrumb End */}

      <div className="container py-5">
        <div className="row">
          {/* IMAGE + THUMBNAILS + ZOOM */}
          <div className="col-lg-5 col-md-6 col-sm-12 mb-4">
            <div
              style={{
                overflow: "hidden",
                borderRadius: "10px",
                boxShadow: "0 4px 10px rgba(0,0,0,0.1)",
              }}
            >
              <img
                src={mainImg}
                alt={product.title}
                style={{
                  width: "100%",
                  transition: "transform 0.3s",
                  transform: zoom ? "scale(1.6)" : "scale(1)",
                }}
                onMouseEnter={() => setZoom(true)}
                onMouseLeave={() => setZoom(false)}
              />
            </div>

            <div className="d-flex mt-3 overflow-auto">
              {product.images.map((img, i) => (
                <img
                  key={i}
                  src={img}
                  onClick={() => setMainImg(img)}
                  style={{
                    width: "70px",
                    height: "70px",
                    objectFit: "cover",
                    borderRadius: "7px",
                    marginRight: "10px",
                    cursor: "pointer",
                    border: mainImg === img ? "2px solid black" : "2px solid #ddd",
                    transition: "0.2s",
                  }}
                />
              ))}
            </div>
          </div>

          {/* PRODUCT INFO */}
          <div className="col-lg-7 col-md-6 col-sm-12">
            <h3 className="mb-3">{product.title}</h3>

            <div className="mb-3">
              <h4 className="text-primary">${product.price}</h4>
              {product.oldPrice && (
                <h6 className="text-muted">
                  <del>${product.oldPrice}</del>
                </h6>
              )}
            </div>

            <div className="mb-3">
              <span className="mr-2">
                {Array.from({ length: Math.round(product.rating) }, (_, i) => (
                  <i key={i} className="fa fa-star text-warning mr-1"></i>
                ))}
                {Array.from({ length: 5 - Math.round(product.rating) }, (_, i) => (
                  <i key={`empty-${i}`} className="fa fa-star mr-1" style={{ color: "#ccc" }}></i>
                ))}
              </span>
              <span>({product.rating})</span>
            </div>

            <p className="mb-4">{product.description}</p>

            <div className="d-flex">
              {/* ADD TO CART BUTTON */}
              <button
                className="btn btn-dark px-4 py-2 mr-3"
                onClick={() => {
                  if (inCart) {
                    //       
                    updateItemQuantity(product.id, inCart.quantity + 1);
                  } else {
                    addItem(product);
                  }
                }}
              >
                <i className="fa fa-shopping-cart mr-2"></i>
                {inCart ? `Add More` : `Add to Cart`}
              </button>

              {/* ADD TO FAVORITES BUTTON */}
              <button
                className={`btn px-4 py-2 ${isFavorite ? "btn-danger" : "btn-outline-dark"}`}
                onClick={() => {
                  if (isFavorite) {
                    removeFromFavorites(product.id);
                  } else {
                    addToFavorites(product);
                  }
                }}
              >
                <i className={`fa ${isFavorite ? "fas" : "far"} fa-heart mr-2`}></i>
                {isFavorite ? "Remove from Wishlist" : "Add to Wishlist"}
              </button>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </>
  );
}

export default AllSingleProduct;
