function AllContact() {
  return (
    <>
      <>
        {/* Breadcrumb Start */}
        <div className="container-fluid">
          <div className="row px-xl-5">
            <div className="col-12">
              <nav className="breadcrumb bg-light mb-30">
                <a className="breadcrumb-item text-dark" href="/">
                  Home
                </a>
                <span className="breadcrumb-item active">Contact</span>
              </nav>
            </div>
          </div>
        </div>
        {/* Breadcrumb End */}
        {/* Contact Start */}
        <div className="container-fluid">
          <h2 className="section-title position-relative text-uppercase mx-xl-5 mb-4">
            <span className="bg-secondary pr-3">Contact Us</span>
          </h2>
          <div className="row px-xl-5">
            <div className="col-lg-7 mb-5">
              <div className="bg-light p-30">
                <h4 className="mb-4">
                  Reach out to us! Whether you have questions, custom orders, or
                  feedback about our handmade products, were here to help.
                </h4>
                <form>
                  <div className="control-group mb-3">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Your Name"
                    />
                  </div>
                  <div className="control-group mb-3">
                    <input
                      type="email"
                      className="form-control"
                      placeholder="Your Email"
                    />
                  </div>
                  <div className="control-group mb-3">
                    <input
                      type="text"
                      className="form-control"
                      placeholder="Subject"
                    />
                  </div>
                  <div className="control-group mb-3">
                    <textarea
                      className="form-control"
                      rows={8}
                      placeholder="Message"
                      defaultValue={""}
                    />
                  </div>
                  <div>
                    <button className="btn btn-primary py-2 px-4" type="submit">
                      Send Message
                    </button>
                  </div>
                </form>
              </div>
            </div>
            <div className="col-lg-5 mb-5">
              <div className="bg-light p-30 mb-30">
                <iframe
                  style={{ width: "100%", height: 315 }}
                  src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3450.1234567890123!2d31.235711!3d30.044420!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x145841f1a1a1a1a1%3A0xabcdef1234567890!2sCairo%2C%20Egypt!5e0!3m2!1sen!2seg!4v1603794290143!5m2!1sen!2seg"
                  frameBorder={0}
                  allowFullScreen=""
                  aria-hidden="false"
                  tabIndex={0}
                />
              </div>
              <div className="bg-light p-30 mb-3">
                <p className="mb-2">
                  <i className="fa fa-map-marker-alt text-primary mr-3" />
                  123 Street, Cairo, Egypt
                </p>
                <p className="mb-2">
                  <i className="fa fa-envelope text-primary mr-3" />
                  info@haneedy.com
                </p>
                <p className="mb-2">
                  <i className="fa fa-phone-alt text-primary mr-3" />
                  +012 345 67890
                </p>
              </div>
            </div>
          </div>
        </div>
        {/* Contact End */}
      </>
    </>
  );
}

export default AllContact;
