import React, { useContext, useState } from "react";
import AllCompo from "../Components/AllCompo.jsx";
import Footer from "../Components/Footer.jsx";
import { apiValue } from "../Data/AllData.js";
import { Link } from "react-router-dom";
import { useCart } from "react-use-cart";
import { useFavorites } from "../context/FavoritesContext";

function AllShop() {
  const data = useContext(apiValue);

  // ---------- Filter State ----------
  const [priceFilter, setPriceFilter] = useState("ALL");

  const filterByPrice = (item) => {
    if (priceFilter === "ALL") return true;

    const price = item.price;

    if (priceFilter === "0-100") return price >= 0 && price <= 100;
    if (priceFilter === "100-200") return price >= 100 && price <= 200;
    if (priceFilter === "200-300") return price >= 200 && price <= 300;
    if (priceFilter === "300-400") return price >= 300 && price <= 400;
    if (priceFilter === "400-500") return price >= 400 && price <= 500;

    return true;
  };

  const filteredProducts = data.filter(filterByPrice);

  // ---------- Pagination ----------
  const [currentPage, setCurrentPage] = useState(1);
  const productsPerPage = 9;

  const indexOfLast = currentPage * productsPerPage;
  const indexOfFirst = indexOfLast - productsPerPage;
  const currentProducts = filteredProducts.slice(indexOfFirst, indexOfLast);

  const totalPages = Math.ceil(filteredProducts.length / productsPerPage);

  const goToPage = (page) => {
    if (page < 1 || page > totalPages) return;
    setCurrentPage(page);
  };

  const { addItem } = useCart();

  const {
    items,
    totalItems,
    cartTotal,
    totalUniqueItems,
    updateItemQuantity,
    removeItem,
    isEmpty,
    emptyCart,
  } = useCart();

  const { favorites, addToFavorites, removeFromFavorites } = useFavorites();

  return (
    <>
      <AllCompo />

      {/* Breadcrumb Start */}
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <a className="breadcrumb-item text-dark" href="/">
                Home
              </a>

              <span className="breadcrumb-item active">Shop</span>
            </nav>
          </div>
        </div>
      </div>
      {/* Breadcrumb End */}

      {/* Shop Start */}
      <div className="container-fluid">
        <div className="row px-xl-5">
          {/* Shop Sidebar Start */}
          <div className="col-lg-3 col-md-4">
            {/* Price Filter */}
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-secondary pr-3">Filter by price</span>
            </h5>

            <div className="bg-light p-4 mb-30">
              <form>
                {/* ALL */}
                <div className="custom-control custom-radio d-flex align-items-center justify-content-between mb-3">
                  <input
                    type="radio"
                    name="price"
                    className="custom-control-input"
                    id="price-all"
                    checked={priceFilter === "ALL"}
                    onChange={() => {
                      setPriceFilter("ALL");
                      setCurrentPage(1);
                    }}
                  />
                  <label className="custom-control-label" htmlFor="price-all">
                    All Price
                  </label>
                </div>

                {/* 0 - 100 */}
                <div className="custom-control custom-radio d-flex align-items-center justify-content-between mb-3">
                  <input
                    type="radio"
                    name="price"
                    className="custom-control-input"
                    id="price-1"
                    checked={priceFilter === "0-100"}
                    onChange={() => {
                      setPriceFilter("0-100");
                      setCurrentPage(1);
                    }}
                  />
                  <label className="custom-control-label" htmlFor="price-1">
                    $0 - $100
                  </label>
                </div>

                {/* 100 - 200 */}
                <div className="custom-control custom-radio d-flex align-items-center justify-content-between mb-3">
                  <input
                    type="radio"
                    name="price"
                    className="custom-control-input"
                    id="price-2"
                    checked={priceFilter === "100-200"}
                    onChange={() => {
                      setPriceFilter("100-200");
                      setCurrentPage(1);
                    }}
                  />
                  <label className="custom-control-label" htmlFor="price-2">
                    $100 - $200
                  </label>
                </div>

                {/* 200 - 300 */}
                <div className="custom-control custom-radio d-flex align-items-center justify-content-between mb-3">
                  <input
                    type="radio"
                    name="price"
                    className="custom-control-input"
                    id="price-3"
                    checked={priceFilter === "200-300"}
                    onChange={() => {
                      setPriceFilter("200-300");
                      setCurrentPage(1);
                    }}
                  />
                  <label className="custom-control-label" htmlFor="price-3">
                    $200 - $300
                  </label>
                </div>

                {/* 300 - 400 */}
                <div className="custom-control custom-radio d-flex align-items-center justify-content-between mb-3">
                  <input
                    type="radio"
                    name="price"
                    className="custom-control-input"
                    id="price-4"
                    checked={priceFilter === "300-400"}
                    onChange={() => {
                      setPriceFilter("300-400");
                      setCurrentPage(1);
                    }}
                  />
                  <label className="custom-control-label" htmlFor="price-4">
                    $300 - $400
                  </label>
                </div>

                {/* 400 - 500 */}
                <div className="custom-control custom-radio d-flex align-items-center justify-content-between">
                  <input
                    type="radio"
                    name="price"
                    className="custom-control-input"
                    id="price-5"
                    checked={priceFilter === "400-500"}
                    onChange={() => {
                      setPriceFilter("400-500");
                      setCurrentPage(1);
                    }}
                  />
                  <label className="custom-control-label" htmlFor="price-5">
                    $400 - $500
                  </label>
                </div>
              </form>
            </div>
            {/* End Price Filter */}
          </div>
          {/* END SIDEBAR */}

          {/* Products */}
          <div className="col-lg-9 col-md-8">
            <div className="row pb-3">
              {currentProducts.map((item) => {
                return (
                  <div
                    className="col-lg-4 col-md-6 col-sm-6 pb-1"
                    key={item.id}
                  >
                    <div className="product-item bg-light mb-4">
                      <div className="product-img position-relative overflow-hidden">
                        <img
                          className="img-fluid w-100"
                          src={item.images[0]}
                          alt=""
                          style={{
                            height: "350px",
                            objectFit: "cover",
                          }}
                        />
                        <div className="product-action">
                          <a
                            className="btn btn-outline-dark btn-square"
                            onClick={() => {
                              //      
                              const existingItem = items.find(
                                (i) => i.id === item.id
                              );
                              if (existingItem) {
                                //      
                                if (existingItem.quantity < item.stock) {
                                  updateItemQuantity(
                                    item.id,
                                    existingItem.quantity + 1
                                  );
                                } else {
                                  alert("No more stock available"); //    
                                }
                              } else {
                                //      
                                addItem(item);
                              }
                            }}
                          >
                            <i className="fa fa-shopping-cart" />
                          </a>

                          <a
                            className="btn btn-outline-dark btn-square"
                            onClick={() => {
                              const exists = favorites.find(
                                (i) => i.id === item.id
                              );
                              if (exists) {
                                removeFromFavorites(item.id);
                              } else {
                                addToFavorites(item);
                              }
                            }}
                          >
                            <i
                              className={
                                favorites.find((i) => i.id === item.id)
                                  ? "fas fa-heart text-danger"
                                  : "far fa-heart"
                              }
                            />
                          </a>

                          <Link
                            className="btn btn-outline-dark btn-square"
                            to={`/singleproduct/${item.id}`}
                          >
                            <i className="fa fa-eye" />
                          </Link>
                        </div>
                      </div>

                      <div className="text-center py-4">
                        {item.title}

                        <div className="d-flex align-items-center justify-content-center mt-2">
                          <h5>${item.price}</h5>
                          <h6 className="text-muted ml-2">
                            <del>$oldPrice{item.oldPrice}</del>
                          </h6>
                        </div>

                        {/* Rating */}
                        <div className="d-flex align-items-center justify-content-center mb-1">
                          {Array.from(
                            { length: Math.round(item.rating) },
                            (_, i) => (
                              <small
                                key={i}
                                className="fa fa-star text-primary mr-1"
                              />
                            )
                          )}

                          {Array.from(
                            { length: 5 - Math.round(item.rating) },
                            (_, i) => (
                              <small
                                key={`empty-${i}`}
                                className="fa fa-star mr-1"
                                style={{ color: "#ccc" }}
                              />
                            )
                          )}

                          <small>({item.rating})</small>
                        </div>

                        {/* Stock */}
                        <div className="text-center">
                          {item.stock > 5 && (
                            <span className="text-success">
                              <i className="fa fa-check-circle mr-1"></i>
                              In Stock
                            </span>
                          )}

                          {item.stock <= 5 && item.stock > 0 && (
                            <span className="text-warning">
                              <i className="fa fa-exclamation-triangle mr-1"></i>
                              Only {item.stock} left!
                            </span>
                          )}

                          {item.stock === 0 && (
                            <span className="text-danger">
                              <i className="fa fa-times-circle mr-1"></i>
                              Out of Stock
                            </span>
                          )}
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>

            {/* Pagination */}
            <div className="col-12">
              <nav>
                <ul className="pagination justify-content-center">
                  <li
                    className={`page-item ${
                      currentPage === 1 ? "disabled" : ""
                    }`}
                  >
                    <a
                      className="page-link"
                      href="#"
                      onClick={() => goToPage(currentPage - 1)}
                    >
                      Previous
                    </a>
                  </li>

                  {Array.from({ length: totalPages }, (_, i) => (
                    <li
                      key={i}
                      className={`page-item ${
                        currentPage === i + 1 ? "active" : ""
                      }`}
                    >
                      <a
                        className="page-link"
                        href="#"
                        onClick={() => goToPage(i + 1)}
                      >
                        {i + 1}
                      </a>
                    </li>
                  ))}

                  <li
                    className={`page-item ${
                      currentPage === totalPages ? "disabled" : ""
                    }`}
                  >
                    <a
                      className="page-link"
                      href="#"
                      onClick={() => goToPage(currentPage + 1)}
                    >
                      Next
                    </a>
                  </li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </div>

      <Footer />
    </>
  );
}

export default AllShop;
