import React from "react";
import { NavLink, Link } from "react-router-dom";
import { useCart } from "react-use-cart";
import { useFavorites } from "../context/FavoritesContext";

function Navbar() {
  const { totalItems } = useCart();
  const { favorites } = useFavorites();

  return (
    <>
      <div className="container-fluid bg-dark mb-30">
        <div className="row px-xl-5">
          <div className="col-lg-3 d-none d-lg-block">
            {/* ... Vertical Categories ... */}
          </div>
          <div className="col-lg-9">
            <nav className="navbar navbar-expand-lg bg-dark navbar-dark py-3 py-lg-0 px-0">
              <a href="/" className="text-decoration-none d-block d-lg-none">
                <span className="h1 text-uppercase text-dark bg-light px-2">Haneedy.</span>
                <span className="h1 text-uppercase text-light bg-primary px-2 ml-n1">Store</span>
              </a>
              <button
                type="button"
                className="navbar-toggler"
                data-toggle="collapse"
                data-target="#navbarCollapse"
              >
                <span className="navbar-toggler-icon" />
              </button>
              <div className="collapse navbar-collapse justify-content-between" id="navbarCollapse">
                <div className="navbar-nav mr-auto py-0">
                  <NavLink to="/" className="nav-item nav-link">Home</NavLink>
                  <NavLink to="/shop" className="nav-item nav-link">Shop</NavLink>
                  <NavLink to="/shopcart" className="nav-item nav-link">Shop Cart</NavLink>
                  <NavLink to="/checkout" className="nav-item nav-link">Checkout</NavLink>
                  <NavLink to="/contact" className="nav-item nav-link">Contact</NavLink>
                </div>
                <div className="navbar-nav ml-auto py-0 ">
                  {/* Favorites */}
                  <Link to="/shopfavorites" className="btn px-0 position-relative">
                    <i className="fas fa-heart text-primary fa-lg" />
                    {favorites.length > 0 && (
                      <span
                        className="badge text-white bg-danger rounded-circle position-absolute d-none d-lg-inline"
                        style={{
                          top: "-5px",
                          right: "-10px",
                          fontSize: "0.7rem",
                          padding: "2px 5px",
                        }}
                      >
                        {favorites.length}
                      </span>
                    )}
                  </Link>

                  {/* Cart */}
                  <NavLink to="/shopcart" className="btn px-0 ml-3">
                    <i className="fas fa-shopping-cart text-primary" />
                    <span
                      className="badge text-secondary border border-secondary rounded-circle"
                      style={{ paddingBottom: 2 }}
                    >
                      {totalItems}
                    </span>
                  </NavLink>
                </div>
              </div>
            </nav>
          </div>
        </div>
      </div>
    </>
  );
}

export default Navbar;
