import AllCompo from "../Components/AllCompo";
import Footer from "../Components/Footer";
import { useCart } from "react-use-cart";
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";


function Cart() {
  const navigate = useNavigate();
  const {
    items,
    totalItems,
    cartTotal,
    totalUniqueItems,
    updateItemQuantity,
    removeItem,
    isEmpty,
    emptyCart,
  } = useCart();

  if (isEmpty) {
    return (
      <>
        <div
          className="d-flex flex-column justify-content-center align-items-center"
          style={{ height: "40vh" }}
        >
          <i className="bi bi-cart-x fa-5x text-primary mb-4"></i>

          <h3 className="text-center mb-4">Your Cart is Empty...</h3>
          <Link to="/shop" className="btn btn-primary btn-lg">
            Back to Shop
          </Link>
        </div>
      </>
    );
  }

  return (
    <>
      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-lg-8 table-responsive mb-5">
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-secondary pr-3">
                Shopping Cart ({totalUniqueItems})
              </span>
            </h5>

            <table className="table table-light table-borderless table-hover text-center mb-0">
              <thead className="thead-dark">
                <tr>
                  <th>Products</th>
                  <th>Price</th>
                  <th>Quantity</th>
                  <th>Total</th>
                  <th>Remove</th>
                </tr>
              </thead>
              <tbody className="align-middle">
                {items.map((item) => {
                  return (
                    <tr>
                      <td className="align-middle">
                        <img
                          src={item.images[0]}
                          alt=""
                          style={{ width: 50 }}
                        />{" "}
                        {item.title}
                      </td>
                      <td className="align-middle">${item.price}</td>
                      <td className="align-middle">
                        <div
                          className="input-group quantity mx-auto"
                          style={{ width: 100 }}
                        >
                          <div className="input-group-btn">
                            <button
                              onClick={() =>
                                updateItemQuantity(item.id, item.quantity - 1)
                              }
                              className="btn btn-sm btn-primary btn-minus"
                            >
                              <i className="fa fa-minus" />
                            </button>
                          </div>
                          <input
                            readOnly
                            type="text"
                            className="form-control form-control-sm bg-secondary border-0 text-center"
                            value={item.quantity}
                          />
                          <div className="input-group-btn">
                            <button
                              onClick={() => {
                                if (item.quantity < item.stock) {
                                  updateItemQuantity(
                                    item.id,
                                    item.quantity + 1
                                  );
                                }
                              }}
                              className="btn btn-sm btn-primary btn-plus"
                            >
                              <i className="fa fa-plus" />
                            </button>
                          </div>
                        </div>
                      </td>
                      <td className="align-middle">
                        ${Math.ceil(item.price * item.quantity)}
                      </td>
                      <td className="align-middle">
                        <button
                          onClick={() => removeItem(item.id)}
                          className="btn btn-sm btn-danger"
                        >
                          <i className="fa fa-times" />
                        </button>
                      </td>
                    </tr>
                  );
                })}
              </tbody>
            </table>
          </div>
          <div className="col-lg-4">
            <form className="mb-30" action="">
              <div className="input-group">
                <input
                  type="text"
                  className="form-control border-0 p-4"
                  placeholder="Coupon Code"
                />
                <div className="input-group-append">
                  <button className="btn btn-primary">Apply Coupon</button>
                </div>
              </div>
            </form>
            <h5 className="section-title position-relative text-uppercase mb-3">
              <span className="bg-secondary pr-3">Cart Summary</span>
            </h5>
            <div className="bg-light p-30 mb-5">
              <div className="border-bottom pb-2">
                <div className="d-flex justify-content-between mb-3">
                  <h6>Subtotal</h6>
                  <h6>${Math.ceil(cartTotal)}</h6>
                </div>
                <div className="d-flex justify-content-between">
                  <h6 className="font-weight-medium">Shipping</h6>
                  <h6 className="font-weight-medium">$15</h6>
                </div>
              </div>
              <div className="pt-2">
                <div className="d-flex justify-content-between mt-2">
                  <h5>Total</h5>
                  <h5>${Math.ceil(cartTotal + 15)}</h5>
                </div>
                <button
                  onClick={() =>
                    navigate("/checkout", { state: { items, cartTotal } })
                  }
                  className="btn btn-block btn-primary font-weight-bold my-3 py-3"
                >
                  Proceed To Checkout
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

function AllShopCart() {
  return (
    <>
      <AllCompo />

      <div className="container-fluid">
        <div className="row px-xl-5">
          <div className="col-12">
            <nav className="breadcrumb bg-light mb-30">
              <a className="breadcrumb-item text-dark" href="/">
                Home
              </a>
              <a className="breadcrumb-item text-dark" href="/shop">
                Shop
              </a>
              <span className="breadcrumb-item active">Shopping Cart</span>
            </nav>
          </div>
        </div>
      </div>

      <Cart />
      <Footer />
    </>
  );
}

export default AllShopCart;
